.blog-header{
    background: #650300;
    color:#FFFFFF;
    display: flex;
    // justify-content: space-between;

}
.blog-header .bh-menu{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin: 8px auto;

    .blog-header-zone{
        display: flex;
        justify-content: space-between;
        align-items: center;
        li{
            margin-right: 48px;
            a{
                font-size: 20px;
                line-height: 30px;
                text-decoration: none;
                font-weight: 700;
                color: #fff;
            }
            a:hover{
                color: #AA0601;
              }
        }   
    }
    a{           
    color: #fff;
    text-decoration: none;
    font-size: 40px;
    width: 40px;
    height: 40px;
}
}


    .blogcontent{
        display: flex;
        flex-direction: column;
        h1{
            margin-bottom: 32px;
        }
        .bloglist{
            margin-bottom: 64px;
            li{
                display: flex;
                margin-bottom: 60px;
                
                img{
                    width: 636px;
                    height: 400px;
                    object-fit: contain;
                    margin-right: 24px;
                }
                .m-model{
                    display: none;
                }

            }
        }
    }
    .blog-content .blog-txt{
        display: flex;
        flex-direction: column;
        max-height: 400px;
        .blog-inform{
            display: flex;
            justify-content: space-between;
            align-items: flex-end;
            margin-bottom: 16px;
        }
        .blog-inform .blog-title{
            h2{
                color:#650300;
                font-weight: 400;
                font-size: 32px;
                line-height: 48px;
            }
            h3{
                color: #000000;
                font-weight: 500;
                font-size: 20px;
                line-height: 30px;
            }
        }
        .blog-inform h4{
            color: #000000;
            font-weight: 500;
            font-size: 16px;
            line-height: 24px;
            opacity: 0.7;
        }
        .b-world{
            height: 100%;
            display: flex;
            flex-direction: column;
            
            justify-content: space-between;
            
            
        p{
            font-size: 20px;
            line-height: 30px;
            color: #555555;
            font-weight: 400;
            text-align: left;
            
            // margin-bottom: 102px;
            .onoff{
                display: none;

            }
        }
        a{ 
            
            font-size: 36px;
            line-height: 54px;
            color: #650300;
            font-weight: 900;
            text-align: right;
            text-decoration: none;
        }}
    }



    .page-change{
        display: flex;
        justify-content: center;
        // margin-top: 64px;
        margin-bottom: 80px;
        li{
            color: #555555;

            a{
                border: 1px solid #555555;
                display: block;
                padding-top: 12px;
                padding-bottom: 12px;
                padding-left: 16px;
                padding-right: 16px; 
                
                font-size: 18px;
                line-height: 27px;
                text-decoration: none;
                color: #555555;
            }
            a:hover{
            background: #000;
            color: #fff;
            }
            .point-page{
                color: #FFFFFF;
                background-color: #000000;
            }
            .last-page{
                padding: 11px 12px;
                font-size: 19px;
                line-height: 29px;
            }
            .next-page{
                background-color: #F2F2F2;
                font-size: 19px;
                line-height: 29px;
                padding: 11px 12px;
            }
            .normal-page{
                text-decoration: none;
            }
        }
    }


